import { useState } from 'react';
import { Platform } from 'react-native';
import { supabase } from '../utils/supabase';
import { GOOGLE_CLIENT_ID } from '../config/env';

// Get the Google OAuth redirect URL for web
const getRedirectUrl = () => {
  if (Platform.OS !== 'web') {
    alert('Not a web platform');
    return undefined;
  }

  // Get the current origin and log all relevant URL information
  const origin = window.location.origin;
  const hostname = window.location.hostname;
  
  alert(`Debug Info - Origin: ${origin}, Hostname: ${hostname}`);
  
  // Use environment-specific redirect URLs
  if (hostname === 'localhost' || hostname.includes('127.0.0.1')) {
    const redirectUrl = 'http://localhost:19006';
    alert(`Development redirect: ${redirectUrl}`);
    return redirectUrl;
  }
  
  // For staging and production, use the actual origin without /auth/callback
  // Supabase will append the necessary path
  alert(`Production/staging redirect: ${origin}`);
  return origin;
};

// Make sure to configure these URLs in Google Cloud Console:
// Development:
// - Origins: http://localhost:19006, http://localhost:3000
// - Redirect: http://localhost:3000/auth/callback
//
// Staging:
// - Origin: https://staging.foilquiver.com
// - Redirect: https://staging.foilquiver.com/auth/callback
//
// Production:
// - Origin: https://foilquiver.com
// - Redirect: https://foilquiver.com/auth/callback

export const useGoogleAuth = () => {
  const [isLoading, setIsLoading] = useState(false);

  const signInWithGoogle = async () => {
    try {
      setIsLoading(true);
      if (Platform.OS !== 'web') {
        alert('Mobile authentication not yet implemented');
        return { data: null, error: new Error('Mobile authentication not yet implemented') };
      }

      const redirectTo = getRedirectUrl();
      alert(`Starting OAuth with redirect: ${redirectTo}`);

      const { data, error } = await supabase.auth.signInWithOAuth({
        provider: 'google',
        options: {
          redirectTo,
          queryParams: {
            access_type: 'offline',
            prompt: 'consent',
          },
        },
      });

      if (error) {
        const errorDetails = {
          message: error.message,
          status: error.status,
          name: error.name,
          stack: error.stack,
        };
        alert(`OAuth error details: ${JSON.stringify(errorDetails, null, 2)}`);
      }

      return { data, error };
    } catch (error) {
      const errorMessage = error instanceof Error ? 
        `${error.name}: ${error.message}\nStack: ${error.stack}` : 
        'Unknown error';
      alert(`Unexpected error details: ${errorMessage}`);
      return { data: null, error };
    } finally {
      setIsLoading(false);
    }
  };

  return {
    signInWithGoogle,
    isLoading,
  };
}; 